/*-------------------SERVICES---------------------------*/

.services{
    margin-top: 20px;
}

.services select{
    width: 90%;
    height: 29px;
    margin-bottom: 10px;
}

.services .new-entry{
    float: right;
    margin-right: 25px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.services .table{
    margin: auto;
    width: 90%;
    vertical-align: middle;
}

.services tbody .td-name{
    text-align: left;
    font-size: 12px;

}

.services thead .th-name{
    text-align: left;

}

.services tbody .td-option{
    text-align: right;
    width: 150px;

}
.services tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.services thead .th-option{
    text-align: right;

}

/*---------------------SERVICE FORM-------------------------*/

.services-form{
    background-color: #EFEFEF;
    width: 90%;
    display: inline-block;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    
}

.services-form input{
    width: 95%;
}

.services-form button{
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    background-color: white;
}