/*-------------------PERSONAL---------------------------*/

.personal{
    margin-top: 20px;
}

.personal select{
    width: 90%;
    height: 29px;
    margin-bottom: 10px;
}

.personal .new-entry{
    float: right;
    margin-right: 25px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.personal .table{
    margin: auto;
    width: 90%;
    vertical-align: middle;
}

.personal tbody .td-name{
    text-align: left;
    font-size: 12px;

}

.personal thead .th-name{
    text-align: left;

}

.personal tbody .td-option{
    text-align: right;
    width: 150px;

}
.personal tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.personal thead .th-option{
    text-align: right;

}

/*---------------------PERSONAL FORM-------------------------*/

.personal-form{
    background-color: #EFEFEF;
    width: 90%;
    display: inline-block;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    
}

.personal-form input{
    width: 95%;
    margin-top: 10px;
}

.personal-form select{
    width: 95%;
    margin-top: 10px;
    margin-bottom: 0px;
}

.personal-form button{
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    background-color: white;
}