/*--------------DEPARTMENTS------------------------*/

.departments{
    margin-top: 20px;
}

.departments .new-entry{
    float: right;
    margin-right: 25px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.departments .table{
    margin: auto;
    width: 90%;
    vertical-align: middle;
}

.departments tbody .td-name{
    text-align: left;
    font-size: 12px;
}

.departments thead .th-name{
    text-align: left;
}

.departments tbody .td-option{
    text-align: right;

}
.departments tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.departments thead .th-option{
    text-align: right;

}

/*---------------------DEPARTEMENT FORM-------------------------*/

.department-form{
    background-color: #EFEFEF;
    width: 90%;
    display: inline-block;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    
}

.department-form input{
    width: 95%;
}

.department-form button{
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    background-color: white;
}