@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  
}

.fade-appear,
.fade-enter{
  opacity: 0;
  z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active{
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}

.fade-exit{
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 150ms linear;
}
.datainput{
    margin-top: 20px;
    position: absolute;
    top: 80px;
    padding: 0px 10px;
    left: 0;
    right: 0;
}

.datainput a{
    text-decoration: none;
}

.datainput h6{
    color: rgb(53, 155, 53);

}

.options{
    margin: 0px auto;
    display: flex
}

.input-button{
    background: white;
    height: 100px;
    border-radius: 10px;
    cursor: pointer;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin-bottom: 15px;
    border: 5px solid rgb(175, 199, 175);
    box-shadow: 2px 2px 5px rgb(177, 174, 174);
    transition: all 0.3s;
 
}

.input-button:hover{
    background: #b3dfb3;    
}

.input-button a{
    text-decoration: none;
    color: black;
}


@media (max-width: 991px) {
        
    .datainput{
        margin-top: 20px;
        margin-top: 20px;
    }
    
    .datainput h3{
        color:  #00A859;
        margin-bottom: 20px;
    }
    
    .options{
        margin: 0px auto;
        display: flex
    }
    
    .input-button{
        background: white; 
        height: 100px;
        border-radius: 10px;
        cursor: pointer;
        padding: 5px;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        margin-bottom: 15px;
        box-shadow: 2px 2px 5px rgb(201, 199, 199);
        transition: all 0.3s;
    }

    .input-button:active{
        background: #d2d8d2; 
        box-shadow: 0px 0px 0px rgb(202, 200, 200), 0px 0px 5px rgb(202, 200, 200) inset;
    }
    
    .input-button a{
        text-decoration: none;
        color: black;
    }
    
    .input-button h3{
        margin: 0px auto;
        position: relative;
        float: right;
    }
  }


.logo{
    width: 120px;
    margin: 5px;
    margin-left: 20px;
}

.navbar{
    box-shadow: 0px 0px 20px rgb(175, 170, 170);
    z-index: 10;
}

.mr-auto a i{
    color:  #00A859;
    margin-right: 10px;
}

.mr-auto a{
    text-decoration: none;
    color:black;
    margin-left: 30px;
}


@media (max-width: 767.98px) {
    
    .mr-auto{
        
        text-align: center;
    }

    .mr-auto a{
        border-radius: 10px;
        margin: 10px 20px;
        background-color: #45af7e;
        padding: 10px;
        color: white;
        box-shadow: 3px 3px 5px rgb(207, 204, 204);

    }

    .mr-auto a i{
       color:  #c5f1dd;
    }
   
    .navbar{
        padding-right: 20px;
        padding-left: 20px;
    }
  }


/*-------------------GOBACK--------------------------*/
.goback{
    display:none
}

@media (max-width: 991px) {
        
    .goback{
        display:block;
        background: linear-gradient(#00A859, #6f7c6f);    
        width: 50px;
        height: 50px;
        border-radius: 100px;
        position: fixed;
        bottom: 3%;
        left: 82%;
        box-shadow: 2px 2px 10px rgb(158, 157, 157);
    }
    
    .goback i{
        position: relative;
        color: white;
        font-size: 25px;
        top: 7px;
    }
  }



/*--------------DEPARTMENTS------------------------*/

.departments{
    margin-top: 20px;
}

.departments .new-entry{
    float: right;
    margin-right: 25px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.departments .table{
    margin: auto;
    width: 90%;
    vertical-align: middle;
}

.departments tbody .td-name{
    text-align: left;
    font-size: 12px;
}

.departments thead .th-name{
    text-align: left;
}

.departments tbody .td-option{
    text-align: right;

}
.departments tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.departments thead .th-option{
    text-align: right;

}

