/*-------------------GUARDS---------------------------*/

.guard{
    margin-top: 20px;
}

.guard select{
    width: 90%;
    height: 29px;
    margin-bottom: 10px;
}

.guard .new-entry{
    float: right;
    margin-right: 25px;
    margin-bottom: 10px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.guard .table{
    margin: auto;
    width: 90%;
    vertical-align: middle;
}

.guard tbody .td-name{
    text-align: left;
    font-size: 12px;

}

.guard thead .th-name{
    text-align: left;

}

.guard tbody .td-option{
    text-align: right;
    width: 150px;

}
.guard tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.guard thead .th-option{
    text-align: right;

}

/*---------------------GUARD FORM-------------------------*/

.guard-form{
    background-color: #EFEFEF;
    width: 90%;
    display: inline-block;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    
}

.guard-form input{
    width: 95%;
}

.guard-form select{
    width: 95%;
    margin-top: 10px;
    margin-bottom: 0px;
}

.guard-form button{
    float: right;
    margin-right: 10px;
    margin-top: 10px;
    background-color: white;
}

.form-check .checkbox{
    width: 20px;
    height: 20px;
    margin-left: 0px;
    margin-top: 15px;
}

.form-check .form-check-label{
    float: left;
    margin-left: 10px;
    margin-top: 12px;
}
