.datainput{
    margin-top: 20px;
    position: absolute;
    top: 80px;
    padding: 0px 10px;
    left: 0;
    right: 0;
}

.datainput a{
    text-decoration: none;
}

.datainput h6{
    color: rgb(53, 155, 53);

}

.options{
    margin: 0px auto;
    display: flex
}

.input-button{
    background: white;
    height: 100px;
    border-radius: 10px;
    cursor: pointer;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin-bottom: 15px;
    border: 5px solid rgb(175, 199, 175);
    box-shadow: 2px 2px 5px rgb(177, 174, 174);
    transition: all 0.3s;
 
}

.input-button:hover{
    background: #b3dfb3;    
}

.input-button a{
    text-decoration: none;
    color: black;
}


@media (max-width: 991px) {
        
    .datainput{
        margin-top: 20px;
        margin-top: 20px;
    }
    
    .datainput h3{
        color:  #00A859;
        margin-bottom: 20px;
    }
    
    .options{
        margin: 0px auto;
        display: flex
    }
    
    .input-button{
        background: white; 
        height: 100px;
        border-radius: 10px;
        cursor: pointer;
        padding: 5px;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        margin-bottom: 15px;
        box-shadow: 2px 2px 5px rgb(201, 199, 199);
        transition: all 0.3s;
    }

    .input-button:active{
        background: #d2d8d2; 
        box-shadow: 0px 0px 0px rgb(202, 200, 200), 0px 0px 5px rgb(202, 200, 200) inset;
    }
    
    .input-button a{
        text-decoration: none;
        color: black;
    }
    
    .input-button h3{
        margin: 0px auto;
        position: relative;
        float: right;
    }
  }

